.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.GalleryBlock .slick-arrow {
  color: transparent;
  width: 35px;
  height: 35px;
  background-color: rgba(255, 255, 255, 0.6);
  background-size: 20px;
  background-size: 24px;
  background-position: 50%;
  background-repeat: no-repeat;
  border: none;
  outline: none;
  position: absolute;
  top: 0%;
  display: block;
  z-index: 42;
  padding: 0;
  cursor: pointer;
}
.GalleryBlock .slick-arrow:hover {
  background-color: #28497c;
}
.GalleryBlock .slick-prev.slick-arrow {
  background-image: url("../../../../../assets/images/icons/blue/arr_1left.svg");
}
.GalleryBlock .slick-prev.slick-arrow:hover {
  background-image: url("../../../../../assets/images/icons/white/arr_1left.svg");
}
.GalleryBlock .slick-next.slick-arrow {
  background-image: url("../../../../../assets/images/icons/blue/arr_1right.svg");
  right: 0;
}
.GalleryBlock .slick-next.slick-arrow:hover {
  background-image: url("../../../../../assets/images/icons/white/arr_1right.svg");
}
.GalleryBlock .slick-list.draggable {
  margin: 0 auto 1em;
  max-width: 100%;
}
.GalleryBlock .slick-list.draggable .slick-slide img {
  max-width: 100%;
}
.GalleryBlock .cw-gallery img {
  margin: auto;
}
.GalleryBlock .cw-gallery .cw-gallery-image-name {
  text-align: center;
  color: #888;
  font-style: italic;
  margin-top: 0.75em;
}
.cw-gallery-folder {
  height: 16px;
  width: 16px;
  padding-left: 1em;
  display: inline-block;
  vertical-align: top;
  background-repeat: no-repeat;
  border: none;
  background-image: url("../../../../../assets/images/icons/blue/folder-empty.svg");
}
.cw-gallery-folder.cw-gallery-folder-full {
  background-image: url("../../../../../assets/images/icons/blue/folder-full2.svg");
}



