.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.ImageMapBlock .block-content {
  overflow-x: auto;
}
.ImageMapBlock .block-content[data-view="student"] {
  text-align: center;
}
.ImageMapBlock .cw-image-map-buttonset {
  display: inline-block;
  padding: 5px;
  margin-right: 0.5em;
}
.ImageMapBlock .cw-image-map-buttonset button {
  cursor: pointer;
  border: solid thin #ccc;
  height: 32px;
  width: 32px;
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px 24px;
  vertical-align: bottom;
}
.ImageMapBlock .cw-image-map-buttonset button.add-shape {
  padding-left: 1.5em;
  width: unset;
  background-position: 6px center;
  background-size: 16px 16px;
  color: #28497c;
  min-width: 7em;
  background-image: url("../../../../../assets/images/icons/blue/add.svg");
}
.ImageMapBlock .cw-image-map-buttonset button.remove-shape {
  background-image: url("../../../../../assets/images/icons/blue/trash.svg");
}
.ImageMapBlock .cw-image-map-buttonset button.cw-image-map-color[value=transparent] {
  background-image: url("../../../../../assets/images/icons/lightblue/decline.svg");
}
.ImageMapBlock .cw-image-map-buttonset button.cw-image-map-color.selected-color {
  border: solid 2px #000;
}
.ImageMapBlock .cw-image-map-buttonset button.cw-image-map-resize {
  padding-left: 1.5em;
  width: unset;
  background-position: 6px center;
  background-size: 16px 16px;
  color: #28497c;
  min-width: 5.5em;
}
.ImageMapBlock .cw-image-map-buttonset button.cw-image-map-resize.raise-size {
  background-image: url("../../../../../assets/images/icons/blue/add.svg");
}
.ImageMapBlock .cw-image-map-buttonset button.cw-image-map-resize.reduce-size {
  background-image: url("../../../../../assets/images/icons/blue/remove.svg");
}
.ImageMapBlock .cw-image-map-data-input.disabled {
  color: #ccc;
}
section#courseware .block-content:not([data-view="student"]) select.cw-image-map-link-type,
section#courseware .block-content:not([data-view="student"]) select.cw-image-map-source {
  width: 15em;
}
section#courseware .block-content:not([data-view="student"]) select.cw-image-map-shape-link-protocol {
  width: 6em;
}
section#courseware .block-content:not([data-view="student"]) input.cw-image-map-shape-link-target {
  width: 23.75em;
  margin-left: 0;
}
section#courseware .block-content:not([data-view="student"]) select.cw-image-map-source {
  width: 9em;
}
section#courseware .block-content:not([data-view="student"]) select:disabled,
section#courseware .block-content:not([data-view="student"]) input:disabled {
  border-color: #ccc;
  color: #ccc;
}
section#courseware.view-student .block-content .cw-image-from-canvas {
  border: solid thin #ccc;
  padding: 3px;
}



