.CodeBlock pre {
  margin-bottom: 0;
}
.CodeBlock .hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #fcfcfc;
  color: black;
  border: solid thin #f3f3f3;
}
.CodeBlock .hljs-comment,
.CodeBlock .hljs-quote,
.CodeBlock .hljs-variable {
  color: #008000;
}
.CodeBlock .hljs-keyword,
.CodeBlock .hljs-selector-tag,
.CodeBlock .hljs-selector-class,
.CodeBlock .hljs-built_in,
.CodeBlock .hljs-name,
.CodeBlock .hljs-tag {
  color: #24437c;
  font-weight: 600;
}
.CodeBlock .hljs-string,
.CodeBlock .hljs-title,
.CodeBlock .hljs-section,
.CodeBlock .hljs-attribute,
.CodeBlock .hljs-literal,
.CodeBlock .hljs-template-tag,
.CodeBlock .hljs-template-variable,
.CodeBlock .hljs-type,
.CodeBlock .hljs-addition {
  color: #f5871f;
  font-weight: 400;
}
.CodeBlock .hljs-deletion,
.CodeBlock .hljs-selector-attr,
.CodeBlock .hljs-selector-pseudo,
.CodeBlock .hljs-meta {
  color: #2b91af;
  font-weight: 400;
}
.CodeBlock .hljs-doctag {
  color: #808080;
  font-weight: 400;
}
.CodeBlock .hljs-attr {
  color: #f00;
  font-weight: 400;
}
.CodeBlock .hljs-symbol,
.CodeBlock .hljs-bullet,
.CodeBlock .hljs-link {
  color: #00b0e8;
  font-weight: 400;
}
.CodeBlock .hljs-emphasis {
  font-style: italic;
  font-weight: 400;
}
.CodeBlock .hljs-strong {
  font-weight: 600;
}
.code-content-editor {
  width: 99%;
  height: 20em;
  padding: 6px 0 6px 5px;
}
.code-content-preview {
  height: 20em;
  overflow: auto;
}
.code-content-preview .hljs {
  overflow-x: inherit;
}
.code-lang {
  background: #f7f7f7;
  border: solid thin #f3f3f3;
  padding: 5px 10px;
  text-align: right;
  color: #aaa;
  font-family: monospace;
  text-transform: full-width;
}
ul.cw-code-tabs {
  margin: 0px;
  padding: 0px;
  list-style: none;
  color: #222;
}
ul.cw-code-tabs li {
  border: solid thin #ccc;
  border-bottom: none;
  background: none;
  color: #222;
  display: inline-block;
  padding: 10px 15px;
  cursor: pointer;
}
ul.cw-code-tabs li.active {
  background: #28497c;
  color: #ffffff;
  border-color: #28497c;
}
.cw-code-container {
  display: none;
  background: #ffffff;
  border: solid thin #ccc;
  padding: 5px 10px;
  height: 25em;
}
.cw-code-container.active {
  display: inherit;
}
section#courseware .block-content:not([data-view="student"]) input.cw-code-file-upload {
  border: none;
  padding: 1em 0;
}



