.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.cw-dialogcards-card-content {
  border: solid thin #ccc;
  margin-bottom: 0.5em;
}
.cw-dialogcards-card-content-default {
  display: none;
}
.cw-dialogcards .scene {
  margin: 0 auto;
  width: 440px;
  height: 520px;
  perspective: 880px;
}
.cw-dialogcards .scene:not(:first-child) {
  display: none;
}
.cw-dialogcards .card {
  width: 100%;
  height: 100%;
  transition: transform 1s;
  transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
}
.cw-dialogcards .card.is-flipped {
  transform: rotateY(180deg);
}
.cw-dialogcards .card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #000;
  text-align: center;
  font-weight: bold;
  font-size: 1.2em;
  backface-visibility: hidden;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
}
.cw-dialogcards .card__face img {
  max-width: 380px;
  max-height: 220px;
  margin-top: 1em;
}
.cw-dialogcards .card__face .cw-dialogcards-front-no-image {
  background-image: url("../../../../../assets/images/icons/lightblue/question.svg");
}
.cw-dialogcards .card__face .cw-dialogcards-back-no-image {
  background-image: url("../../../../../assets/images/icons/lightblue/exclaim.svg");
}
.cw-dialogcards .card__face .cw-dialogcards-front-no-image,
.cw-dialogcards .card__face .cw-dialogcards-back-no-image {
  width: 100%;
  height: 180px;
  margin-top: 2em;
  background-repeat: no-repeat;
  background-size: 150px 150px;
  background-position-x: center;
}
.cw-dialogcards .card__face p {
  margin: 1em 3em 1em 4em;
  padding-right: 1em;
  overflow-y: auto;
  max-height: 12em;
  text-align: justify;
}
.cw-dialogcards .card__face--front {
  background-color: #fff;
  background-image: url("../../../../../assets/images/icons/blue/arr_1right.svg");
  background-repeat: no-repeat;
  background-position: 95% 95%;
}
.cw-dialogcards .card__face--back {
  background-color: #fff;
  background-image: url("../../../../../assets/images/icons/blue/arr_1left.svg");
  background-repeat: no-repeat;
  background-position: 5% 95%;
  transform: rotateY(180deg);
}
.cw-dialogcards-navbutton {
  color: transparent;
  width: 35px;
  height: 35px;
  background-color: #bbb;
  background-size: 20px;
  border-radius: 2px;
  background-size: 24px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-color: #28497c;
  border: none;
  outline: none;
  position: absolute;
  top: 50%;
  display: block;
  z-index: 42;
  margin-top: -10px;
  padding: 0;
  cursor: pointer;
}
.cw-dialogcards-navbutton.cw-dialogcards-prev {
  background-image: url("../../../../../assets/images/icons/white/arr_1left.svg");
}
.cw-dialogcards-navbutton.cw-dialogcards-next {
  background-image: url("../../../../../assets/images/icons/white/arr_1right.svg");
  right: 0;
}
button[name="removecard"] {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
  background: url("../../../../../assets/images/icons/blue/trash.svg") no-repeat;
  cursor: pointer;
  float: right;
  margin-top: 20px;
}
.cw-dialogcards-front-img,
.cw-dialogcards-front-img-file,
.cw-dialogcards-back-img,
.cw-dialogcards-back-img-file,
.cw-dialogcards-front-img-info,
.cw-dialogcards-front-img-file-info,
.cw-dialogcards-back-img-info,
.cw-dialogcards-back-img-file-info {
  display: none;
}
section#courseware .DialogCardsBlock .block-content:not([data-view="student"]) label {
  width: 18em;
  margin-left: 0.75em;
}
section#courseware .DialogCardsBlock .block-content:not([data-view="student"]) select.cw-dialogcards-source {
  width: 18em;
  margin-right: 1.75em;
}
section#courseware button.button.cw-dialogcards-addcard {
  color: #28497c;
  border: solid thin #28497c;
  background-color: #fff;
  background-repeat: no-repeat;
  min-height: 27px;
  line-height: 130%;
  min-width: 100px;
  padding: 5px 15px 5px 30px;
  margin: 0 0 0.25em 0;
  cursor: pointer;
  font-size: 14px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  white-space: nowrap;
  width: auto;
  width: initial;
  text-align: left;
  background-image: url("../../../../../assets/images/icons/blue/add.svg");
  background-position: 6px center;
  padding-left: 28px;
}
section#courseware button.button.cw-dialogcards-addcard:hover {
  background-color: #28497c;
  color: #fff;
  outline: 0;
}
section#courseware button.button.cw-dialogcards-addcard:hover {
  background-image: url("../../../../../assets/images/icons/white/add.svg");
}



