.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.videodata {
  margin: 0 1em;
}
.webvideo {
  border: solid thin #ccc;
  margin-bottom: 6px;
  padding: 1em 0;
}
.addmediatype {
  color: #28497c;
  border: solid thin #28497c;
  background-color: #fff;
  background-repeat: no-repeat;
  min-height: 27px;
  line-height: 130%;
  min-width: 100px;
  padding: 5px 15px 5px 30px;
  margin: 0 0 0.25em 0;
  cursor: pointer;
  font-size: 14px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  white-space: nowrap;
  width: auto;
  margin: 0;
  width: initial;
  text-align: left;
  background-image: url("../../../../../assets/images/icons/blue/add.svg");
  background-position: 6px center;
  padding-left: 28px;
  margin-top: 1em;
}
.addmediatype:hover {
  background-color: #28497c;
  color: #fff;
  outline: 0;
}
.addmediatype:hover {
  background-image: url("../../../../../assets/images/icons/white/add.svg");
}
.removemediatype {
  color: #28497c;
  border: solid thin #28497c;
  background-color: #fff;
  background-repeat: no-repeat;
  min-height: 27px;
  line-height: 130%;
  min-width: 100px;
  padding: 5px 15px 5px 30px;
  margin: 0 0 0.25em 0;
  cursor: pointer;
  font-size: 14px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  white-space: nowrap;
  width: auto;
  margin: 0;
  margin-left: 1em;
  width: initial;
  text-align: left;
  background-image: url("../../../../../assets/images/icons/blue/trash.svg");
  background-position: 6px center;
  padding-left: 28px;
}
.removemediatype:hover {
  background-color: #28497c;
  color: #fff;
  outline: 0;
}
.removemediatype:hover {
  background-image: url("../../../../../assets/images/icons/white/trash.svg");
}
.webvideo-description {
  background: #eee;
  margin: 1em 0;
  background-image: url("../../../../../assets/images/icons/blue/info-circle.svg");
  background-size: 32px;
  background-repeat: no-repeat;
  background-position: 26px center;
  padding-left: 64px;
}
.webvideo-description p {
  padding: 20px;
  line-height: 1.3em;
}
.cw-video-empty-recording,
.cw-videoblock-recorder-wrapper {
  display: none;
}
.block-content .video-wrapper {
  margin-bottom: 2em;
  background-color: #ccc;
  position: relative;
  padding-top: 0px;
  height: 0;
  overflow: hidden;
}
.block-content .video-wrapper.aspect-169 {
  padding-bottom: 56.25%;
  /* 16:9 ratio; 9/16*100 */
}
.block-content .video-wrapper.aspect-43 {
  padding-bottom: 75%;
  /* 4:3 ratio; 3/4*100 */
}
.block-content .video-wrapper iframe {
  border: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.block-content .video-wrapper video {
  width: 100%;
}
.block-content .videourl {
  display: none;
}
.block-content .videostatus {
  height: 2em;
}
.block-content .videostatus .status {
  display: none;
}
.block-content .videosettings-header {
  font-size: 14px;
  font-weight: 400;
}
.block-content .videosettings > span:not(:first-child) {
  padding-left: 1em;
}
.block-content .videosettings .videostartmin,
.block-content .videosettings .videostartsec,
.block-content .videosettings .videoendmin,
.block-content .videosettings .videoendsec {
  width: 3em !important;
}
section#courseware button.button.cw-video-timer-reset {
  float: right;
  margin-top: 0.6em;
}
section#courseware .block-content:not([data-view="student"]) label.cw-video-timer-label {
  min-width: 0;
  width: 2em;
}
section#courseware .block-content:not([data-view="student"]) label.cw-video-timer-label.cw-video-timer-label-end {
  padding-left: 4em;
}
.video-header {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
}
/* sidebar special */
.cw-sidebar .video-header {
  background: #e7ebf1;
  color: #28497c;
  font-weight: bold;
  font-size: unset;
  line-height: unset;
  text-align: left;
  padding: 4px;
  margin: 0 0 10px 0;
  border: none;
  position: unset;
  margin-bottom: 0;
  border: 1px solid #d0d7e3;
  border-bottom: none;
}
.cw-sidebar .video-wrapper {
  border: 1px solid #d0d7e3;
  border-top: none;
}
section#courseware .block-content:not([data-view="student"]) select.cw-webvideo-source {
  width: 14em;
  margin-right: 1.75em;
}
section#courseware .block-content:not([data-view="student"]) label.cw-webvideo-label {
  padding-left: 1.75em;
}
section#courseware .block-content:not([data-view="student"]) label.cw-videoblock-recorder-label {
  width: 15em;
}
section#courseware .cw-videoblock-recorder-buttons button.button,
section#courseware button.cw-videoblock-recording-wrapper,
section#courseware button.cw-videoblock-recorder-reset {
  vertical-align: baseline;
}
.cw-webvideo-source-file {
  display: none;
}



