.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
section.TestBlock div.description {
  margin-bottom: 20px;
}
.question_title {
  font-size: 12pt;
  margin-left: 0.25em;
}
.question_description {
  padding: 1em 0 1em 0;
  margin-left: 0.5em;
}
ul.exercise {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
ul.exercise li.exercise-item {
  border: solid thin #ccc;
}
ul.exercise .cw-test-content {
  padding: 10px;
}
ul.exercise:not(#exercise1) {
  display: none;
}
ul.exercise .exercisetitle {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
  text-transform: uppercase;
  border: none;
  border-bottom: solid thin #ccc;
}
ul.exercise .exercisetitle .exercisenavbutton {
  position: absolute;
  border: none;
  height: 20px;
  width: 20px;
  margin: 2px 12px;
  cursor: pointer;
}
ul.exercise .exercisetitle .exercisenavbutton.exercisenavback {
  left: 0;
  background: no-repeat scroll 0 0;
  background-image: url("../../../../../assets/images/icons/blue/arr_1left.svg");
}
ul.exercise .exercisetitle .exercisenavbutton.exercisenavnext {
  right: 0;
  background: no-repeat scroll 0 0;
  background-image: url("../../../../../assets/images/icons/blue/arr_1right.svg");
}
ul.exercise .cw-test-content {
  padding: 0 10px;
  /* vips 1.3 rh_exercise */
  /* vips 1.4 rh_exercise */
}
ul.exercise .cw-test-content .exercise-title {
  text-align: center;
  color: #000;
}
ul.exercise .cw-test-content form {
  text-transform: none;
  font-size: 100%;
  font-weight: 400;
}
ul.exercise .cw-test-content .answer,
ul.exercise .cw-test-content label {
  font-size: 14px;
  font-weight: normal;
  line-height: 32px;
  padding-left: 7px;
  text-transform: none;
  display: block;
}
ul.exercise .cw-test-content .label_text {
  margin-left: 7px;
}
ul.exercise .cw-test-content select {
  margin: 0 5px;
}
ul.exercise .cw-test-content .cloze.solution {
  margin-bottom: 8px;
}
ul.exercise .cw-test-content p img {
  margin-top: 12px;
  margin-bttom: 12px;
}
ul.exercise .cw-test-content textarea {
  border: thin solid #ccc;
  height: 10em;
  width: 100% !important;
}
ul.exercise .cw-test-content input {
  border: thin solid #ccc;
}
ul.exercise .cw-test-content table {
  padding: 1em 0;
}
ul.exercise .cw-test-content table .rh_label {
  padding: 1em;
}
ul.exercise .cw-test-content table .rh_item {
  padding: 1em;
  border: solid thin #ccc;
  cursor: move;
  background: #fff;
}
ul.exercise .cw-test-content table .rh_item:hover {
  border: solid thin #28497c;
}
ul.exercise .cw-test-content .rh_table {
  padding: 1em 0;
  border-spacing: 1.5em;
  margin-left: -1em;
}
ul.exercise .cw-test-content .rh_table .rh_label {
  padding: 1em 1em 1em 0;
  min-width: 240px;
}
ul.exercise .cw-test-content .rh_table .rh_list {
  border: 1px solid #ccc;
  background-color: #fcfcfc;
  min-width: 240px;
}
ul.exercise .cw-test-content .rh_table .rh_list.hover {
  border-color: #28497c;
  background-color: #eee;
}
ul.exercise .cw-test-content .rh_table .rh_item {
  padding: 0.5em;
  border: solid thin #ccc;
  cursor: move;
  background: #fff;
  margin: 0.25em;
  min-width: 240px;
}
ul.exercise .cw-test-content .rh_table .rh_item:hover {
  border: solid thin #28497c;
}
ul.exercise .cw-test-content .ui-sortable .ui-state-highlight {
  padding: 2em;
  background: #28497c;
  border-color: #28497c;
}
ul.exercise .cw-test-content span.correct_item,
ul.exercise .cw-test-content .studip_input_wrapper.correct_item {
  background-color: lightgreen;
}
ul.exercise .cw-test-content .cw-testblock-corrector-comment-corrected {
  background-color: #eee;
  padding: 1em;
}
ul.exercise > li:after {
  content: '';
  display: block;
  clear: both;
  padding-bottom: 10px;
  border-bottom: 1px solid #EEEEEE;
}
ul.exercise button.button {
  display: block;
  clear: both;
  margin: 0.8em 0.6em 0.8em 0 !important;
}
ul.exercise .exercise-hint-button {
  border: none;
  background-image: url("../../../../../assets/images/icons/blue/info-circle.svg");
  background-position: center 0px;
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: 24px;
  height: 24px;
  width: 24px;
  float: right;
  cursor: pointer;
  margin-top: -12px;
}
ul.exercise .exercise-hint {
  margin: 8px;
  padding: 20px 25px;
  background-color: #f8f8f8;
}
ul.exercise p.exercise-tryagain {
  color: red;
}
ul.exercise p.exercise-correct {
  color: green;
}
ul.exercise p.solution .correct_answer {
  color: green;
}
ul.exercise p.solution .incorrect_answer {
  color: red;
}
ul.exercise p.question input[type=text] {
  width: 240px;
}
ul.exercise .caption {
  font-weight: bold;
  margin: 1em 0 0.5em 0;
}
ul.exercise a.edit-tests {
  display: block;
  float: right;
}
ul.exercise fieldset {
  border: none;
  padding-left: 0px;
  padding-top: 0px;
}
ul.exercise fieldset legend {
  background-color: transparent;
  font-weight: bold;
  color: #444444;
  line-height: normal;
  text-indent: 0px;
  position: relative;
  display: block;
  margin-top: 10px;
}
ul.exercise fieldset legend:nth-child(2) {
  margin-bottom: 1em;
}
ul.exercise fieldset > div {
  padding-left: 0px;
  text-indent: 0px;
}
ul.exercise fieldset > div > span {
  white-space: normal !important;
}
ul.exercise .cw-test-buttons-wrapper {
  padding-left: 8px;
}
ul.exercise .exercise-sample-solution {
  border: solid thin #ccc;
  padding: 10px;
}
ul.exercise .exercise-sample-solution span {
  font-weight: 600;
}
/********************/
/* CHARACTER PICKER */
/********************/
#character_picker span.block {
  background-color: #e6e6e6;
  border-radius: 0.25em;
  display: inline-block;
  margin: 0.125em 0.25em;
  padding: 0.0625em 0.25em;
  white-space: nowrap;
}
#character_picker span.block span.block_title {
  background-color: #cfcfcf;
  border-radius: 0.25em 0 0 0.25em;
  display: inline-block;
  font-weight: bold;
  margin-left: -0.125em;
  padding: 0 0.5em;
}
#character_picker span.block button {
  border: 0;
  border-radius: 0.25em;
  padding: 0 0.25em;
}
#character_picker span.block button:hover {
  background-color: #f3f3f3;
}
#character_picker_header {
  background-color: #cccccc;
  border-radius: 0.25em 0.25em 0 0;
  font-size: smaller;
  padding: 0.5em;
  margin: -0.25em -0.25em 0.5em 0.5em;
}
/********************/
/*   MOBILE VIEW    */
/********************/
@media screen and (max-width: 800px) {
  ul.exercise .cw-test-content {
    /* vips 1.3 rh_exercise */
    /* vips 1.4 rh_exercise */
  }
  ul.exercise .cw-test-content .rh_table .rh_label {
    min-width: unset;
  }
  ul.exercise .cw-test-content .rh_table .rh_list {
    min-width: 50px;
  }
  ul.exercise .cw-test-content .rh_table .rh_item {
    min-width: 50px;
  }
}
/*** hide lti-exercise submit button ***/
section#courseware ul.exercise .cw-vips-lti_exercise button.cw-test-submit-button {
  display: none;
}



