.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
section#courseware button.button.cw-audio-gallery-record-button-delete {
  background-image: url("../../../../../assets/images/icons/blue/trash.svg");
  margin-left: 1em;
  background-position: 6px center;
  padding-left: 2em;
}
section#courseware button.button.cw-audio-gallery-record-button-delete:hover {
  background-image: url("../../../../../assets/images/icons/white/trash.svg");
}
section#courseware button.button.cw-audio-gallery-record-button-start {
  background-image: url("../../../../../assets/images/icons/blue/span-full.svg");
  margin-left: 1em;
  background-position: 6px center;
  padding-left: 2em;
}
section#courseware button.button.cw-audio-gallery-record-button-start:hover {
  background-image: url("../../../../../assets/images/icons/white/span-full.svg");
}
section#courseware button.button.cw-audio-gallery-recording-info {
  background-image: url("../../../../../assets/images/icons/red/span-full.svg");
  color: #d60000;
  border-color: #d60000;
  background-position: 6px center;
  padding-left: 2em;
  cursor: progress;
}
section#courseware button.button.cw-audio-gallery-recording-info:hover {
  background-color: #fff;
  color: #d60000;
}
section#courseware button.button.cw-audio-gallery-record-button-stop {
  background-image: url("../../../../../assets/images/icons/blue/stop.svg");
  background-position: 6px center;
  padding-left: 2em;
}
section#courseware button.button.cw-audio-gallery-record-button-stop:hover {
  background-image: url("../../../../../assets/images/icons/white/stop.svg");
}
.AudioGalleryBlock .block-content {
  border: solid thin #ccc;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-for {
  margin: auto 40px;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-for div.cw-audio-gallery-content-slide div.cw-audio-gallery-content-slide-control {
  text-align: center;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-for div.cw-audio-gallery-content-slide div.cw-audio-gallery-content-slide-control img {
  margin: 0 auto 1em auto;
  height: 250px;
  border: solid thin #ccc;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-for div.cw-audio-gallery-content-slide div.cw-audio-gallery-content-slide-control audio {
  margin: auto;
  width: 400px;
  display: inline-block;
  vertical-align: top;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav {
  margin: auto 40px;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-prev {
  left: -36px;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-next {
  right: -36px;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-slide {
  cursor: pointer;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-slide div.cw-audio-gallery-nav-slide {
  background: #fff;
  border: solid thin #28497c;
  color: #28497c;
  font-size: 1em;
  margin: 10px;
  padding: 2%;
  position: relative;
  text-align: left;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-slide div.cw-audio-gallery-nav-slide img {
  height: 60px;
  max-width: 60px;
  display: inline-block;
  border: solid thin #ccc;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-slide div.cw-audio-gallery-nav-slide p {
  display: inline-block;
  margin-left: 5%;
  vertical-align: top;
  margin-right: 5%;
  width: 50%;
  min-height: 60px;
  word-wrap: break-word;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-current div.cw-audio-gallery-nav-slide {
  background-color: #28497c;
  color: #fff;
  font-weight: 600;
  padding: 10px;
  margin: 5px;
}
.AudioGalleryBlock .block-content .cw-audio-gallery-carousel-nav .slick-list.draggable {
  margin: 0 auto 1em;
}
.cw-audio-gallery-record-browser-info {
  display: none;
}



