.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
section#courseware .PostBlock .block-content:not([data-view="student"]) label {
  width: 26em;
}
section#courseware .PostBlock .block-content:not([data-view="student"]) label[for="cw-postblock-id"],
section#courseware .PostBlock .block-content:not([data-view="student"]) .cw-postblock-id {
  display: none ;
}
section#courseware .PostBlock .cw-postblock-header {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
}
section#courseware .PostBlock .cw-postblock-posts {
  margin-bottom: 1em;
}
section#courseware .PostBlock .cw-postblock-posts.shrink-messages {
  max-height: 420px;
  overflow: auto;
}
section#courseware .PostBlock .cw-postblock-messagebox {
  margin: 1em 0;
}
section#courseware .PostBlock .cw-postblock-messagebox .cw-postblock-textbox {
  resize: none;
  height: 3em;
  padding: 1em;
  border: solid thin #ccc;
}
section#courseware .PostBlock .cw-postblock-messagebox .button.cw-postblock-sendmessage {
  margin: 0 -2px 24px 0;
  float: right;
}
.talk-bubble {
  margin: 10px 20px;
  display: inline-block;
  position: relative;
  width: 80%;
  height: auto;
  background-color: #ecf0f1;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
}
.talk-bubble.post {
  float: left;
}
.talk-bubble.own-post {
  float: right;
}
.talk-bubble:after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  top: 0px;
  bottom: auto;
  border: 22px solid;
  border-color: #ecf0f1 transparent transparent transparent;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
}
.talk-bubble.post:after {
  left: -20px;
  right: auto;
}
.talk-bubble.own-post:after {
  left: auto;
  right: -20px;
}
.talk-bubble .post-user {
  padding: 1em 1em 0 1em;
}
.talk-bubble .post-user img {
  vertical-align: bottom;
}
.talk-bubble .post-user p {
  display: inline;
  padding-left: 0.5em;
  color: #aaa;
  font-weight: 600;
}
.talk-bubble .post-user p span {
  font-weight: 400;
}
.talk-bubble .talktext {
  padding: 1em;
  text-align: left;
  line-height: 1.5em;
}
.talk-bubble .talktext .talktext-time {
  color: #aaa;
  text-align: right;
  font-size: 0.8em;
  margin-bottom: -0.5em;
}
/* sidebar special */
.cw-sidebar .PostBlock {
  border: 1px solid #d0d7e3;
}
.cw-sidebar .PostBlock .cw-postblock-header {
  background: #e7ebf1;
  color: #28497c;
  font-weight: bold;
  font-size: unset;
  line-height: unset;
  text-align: left;
  padding: 4px;
  margin: 0 0 10px 0;
  border: none;
  position: unset;
  margin: 0;
}
.cw-sidebar .PostBlock .cw-postblock-textbox {
  border: 1px solid #d0d7e3;
  border-left: none;
  border-right: none;
  max-width: 270px;
}



