.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.cw-download-title {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
  border-bottom: none;
}
.cw-download-file {
  border: solid thin #ccc;
  padding: 1em;
}
.cw-download-info {
  background-image: url("../../../../../assets/images/icons/blue/file.svg");
  background-repeat: no-repeat;
  background-size: 24px;
  padding-left: 26px;
  line-height: 24px;
  margin-top: 4px;
}
.cw-download-info.cw-fileicon-audio {
  background-image: url("../../../../../assets/images/icons/blue/file-audio.svg");
}
.cw-download-info.cw-fileicon-pic {
  background-image: url("../../../../../assets/images/icons/blue/file-pic.svg");
}
.cw-download-info.cw-fileicon-video {
  background-image: url("../../../../../assets/images/icons/blue/file-video.svg");
}
.cw-download-info.cw-fileicon-pdf {
  background-image: url("../../../../../assets/images/icons/blue/file-pdf.svg");
}
.cw-download-info.cw-fileicon-text {
  background-image: url("../../../../../assets/images/icons/blue/file-text.svg");
}
.cw-download-info.cw-fileicon-ppt {
  background-image: url("../../../../../assets/images/icons/blue/file-ppt.svg");
}
.cw-download-info.cw-fileicon-archive {
  background-image: url("../../../../../assets/images/icons/blue/file-archive.svg");
}
.cw-download-info span {
  color: lightgrey;
}
.cw-download-button-wrapper {
  text-align: right;
  margin-top: -2em;
}
.cw-download-button-wrapper .cw-download-button {
  color: #28497c;
  border: solid thin #28497c;
  background-color: #fff;
  min-height: 27px;
  line-height: 130%;
  min-width: 100px;
  padding: 5px 15px 5px 30px;
  margin: 0 0 0.25em 0;
  cursor: pointer;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  white-space: nowrap;
  width: auto;
  height: 35px;
  background-position: 16px center;
  font-size: 14px;
  padding-left: 48px;
  padding-right: 20px;
  display: inline-block;
  background-image: url("../../../../../assets/images/icons/blue/download.svg");
  background-repeat: no-repeat;
}
.cw-download-button-wrapper .cw-download-button:hover {
  background-color: #28497c;
  color: #fff;
  outline: 0;
}
.cw-download-button-wrapper .cw-download-button:hover {
  background-image: url("../../../../../assets/images/icons/white/download.svg");
}
/* sidebar special */
.cw-sidebar .DownloadBlock {
  padding: 0;
}
.cw-sidebar .DownloadBlock .cw-download-title {
  background: #e7ebf1;
  color: #28497c;
  font-weight: bold;
  font-size: unset;
  line-height: unset;
  text-align: left;
  padding: 4px;
  margin: 0 0 10px 0;
  border: none;
  position: unset;
}
.cw-sidebar .DownloadBlock .cw-download-info {
  overflow: hidden;
}
.cw-sidebar .DownloadBlock .cw-download-button-wrapper {
  margin: 1em;
}



