.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
@media screen and (max-width: 800px) {
  .cw-pdf-wrapper {
    width: 99%;
  }
}
.cw-pdf-wrapper .cw-pdf-header {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
  margin: 0 -2px 0 0;
  border-bottom: none;
}
.cw-pdf-wrapper .cw-pdf-header .cw-pdf-button-prev,
.cw-pdf-wrapper .cw-pdf-header .cw-pdf-button-next {
  position: absolute;
  border: none;
  height: 20px;
  width: 20px;
  margin: 2px 12px;
  cursor: pointer;
}
.cw-pdf-wrapper .cw-pdf-header .cw-pdf-button-prev {
  left: 0;
  background: no-repeat scroll 0 0;
  background-image: url("../../../../../assets/images/icons/blue/arr_1left.svg");
}
.cw-pdf-wrapper .cw-pdf-header .cw-pdf-button-next {
  right: 0;
  background: no-repeat scroll 0 0;
  background-image: url("../../../../../assets/images/icons/blue/arr_1right.svg");
}
.cw-pdf-wrapper .cw-pdf-header .cw-pdf-download {
  width: 24px;
  height: 24px;
  background: no-repeat scroll 0 0;
  background-image: url("../../../../../assets/images/icons/blue/download.svg");
  border: none;
  cursor: pointer;
}
section#courseware .block-content:not([data-view="student"]) label {
  width: 14em;
}
/* sidebar special */
.cw-sidebar .cw-pdf-wrapper {
  border: 1px solid #d0d7e3;
}
.cw-sidebar .cw-pdf-wrapper .cw-pdf-header {
  background: #e7ebf1;
  color: #28497c;
  font-weight: bold;
  font-size: unset;
  line-height: unset;
  text-align: left;
  padding: 4px;
  margin: 0 0 10px 0;
  border: none;
  position: unset;
  padding: 4px 50px;
  margin: 0;
}
.cw-sidebar .cw-pdf-wrapper .cw-pdf-canvas {
  width: 268px !important;
  border: none !important;
}



