.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.cw-date-wrapper {
  display: table;
  margin: 0 auto;
  cursor: pointer;
}
.cw-date-wrapper .cw-date-countdown {
  display: none;
}
.cw-date-wrapper .cw-date-countdown .cw-date-countdown-digit {
  display: inline-block;
  margin-right: 4px;
}
.cw-date-wrapper .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-number {
  font-size: 6em;
  line-height: 1.25em;
  height: 1.25em;
  padding: 3px 29px;
  background: #f5f5f5;
  font-weight: 300;
}
.cw-date-wrapper .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-label-sg,
.cw-date-wrapper .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-label-pl {
  display: none;
  padding: 5px;
  font-size: 1.25em;
  text-align: left;
  text-transform: uppercase;
}
.cw-date-wrapper .cw-date-date {
  display: none;
}
.cw-date-wrapper .cw-date-date .cw-date-date-space {
  display: inline-block;
  width: 2em;
}
.cw-date-wrapper .cw-date-date .cw-date-date-digits {
  display: inline-block;
}
.cw-date-wrapper .cw-date-date .cw-date-date-digits .cw-date-date-number {
  font-size: 5em;
  line-height: 1.25em;
  height: 1.25em;
  padding: 0.25em;
  background: #f5f5f5;
  font-weight: 300;
}
.cw-sidebar .cw-date-countdown .cw-date-countdown-digit {
  margin: 0;
}
.cw-sidebar .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-number {
  font-size: 1.75em;
  padding: 0.7em;
}
.cw-sidebar .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-label-sg,
.cw-sidebar .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-label-pl {
  padding: 2px;
  font-size: 0.75em;
}
.cw-sidebar .cw-date-date .cw-date-date-space {
  width: 0.5em;
}
.cw-sidebar .cw-date-date .cw-date-date-digits .cw-date-date-number {
  font-size: 2em;
  padding: 4px 10px;
}
@media screen and (max-width: 800px) {
  .cw-date-countdown .cw-date-countdown-digit {
    margin: 0;
  }
  .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-number {
    font-size: 2.15em;
    padding: 0.75em;
  }
  .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-label-sg,
  .cw-date-countdown .cw-date-countdown-digit .cw-date-countdown-label-pl {
    padding: 2px;
    font-size: 0.75em;
  }
}



