.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.cw-link {
  padding: 1em 1em 1em 5.5em;
  border: solid thin #ccc;
  color: #28497c;
}
.cw-link:hover {
  border: solid thin #28497c;
  color: #fff;
  background-color: #28497c;
}
.cw-link.internal {
  background-image: url("../../../../../assets/images/icons/blue/link-intern.svg");
  background-position: 2em 50%;
  background-repeat: no-repeat;
  background-size: 28px;
}
.cw-link.internal .cw-link-progress-true,
.cw-link.internal .cw-link-progress-false {
  background-repeat: no-repeat;
  background-size: 16px;
  height: 16px;
  width: 16px;
  display: inline-block;
}
.cw-link.internal .cw-link-progress-true {
  background-image: url("../../../../../assets/images/icons/green/accept.svg");
  vertical-align: top;
}
.cw-link.internal .cw-link-progress-false {
  background-image: url("../../../../../assets/images/icons/red/decline.svg");
  vertical-align: middle;
}
.cw-link.internal:hover {
  background-image: url("../../../../../assets/images/icons/white/link-intern.svg");
}
.cw-link.external {
  background-image: url("../../../../../assets/images/icons/blue/link-extern.svg");
  background-position: 2em 50%;
  background-repeat: no-repeat;
  background-size: 28px;
}
.cw-link.external:hover {
  background-image: url("../../../../../assets/images/icons/white/link-extern.svg");
}
.cw-link.external .cw-link-og-image {
  display: inline-block;
  max-width: 40%;
  vertical-align: top;
  margin-right: 2em;
}
.cw-link.external .cw-link-og-textwrapper {
  display: inline-block;
  max-width: 50%;
}
.cw-link.external .cw-link-og-textwrapper p {
  margin: 0;
}
.cw-link.external .cw-link-og-textwrapper .cw-link-og-site {
  font-size: 1.25em;
}
.cw-link.external .cw-link-og-textwrapper .cw-link-og-title {
  font-weight: 600;
}
.cw-link.external .cw-link-og-textwrapper .cw-link-og-description {
  color: #687fa3;
  text-align: justify;
}
section#courseware .block-content:not([data-view="student"]) select.cw-link-protocol {
  width: 6em;
}
section#courseware .block-content:not([data-view="student"]) input.cw-link-target {
  margin-left: 0;
  width: 23.75em;
}



