.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.EmbedBlock .cw-embedblock-title {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
}
.EmbedBlock .cw-embedblock-iframe {
  width: 870px !important;
}
.EmbedBlock .cw-embedblock-image.full-width {
  width: 870px;
  height: auto;
}
.EmbedBlock .cw-embedblock-link {
  list-style: none;
  display: inline-block;
  margin-left: -1.5em;
}
.EmbedBlock .cw-embedblock-link li {
  background-image: url("../../../../../assets/images/icons/blue/link-extern.svg");
  background-position: 0 50%;
  background-repeat: no-repeat;
  background-size: 16px;
  display: none;
}
.EmbedBlock .cw-embedblock-link li a {
  padding-left: 1.5em;
}
.EmbedBlock .cw-embedblock-info {
  color: #ccc;
}
.EmbedBlock .cw-embedblock-about {
  background: #eee;
  margin: 1em 0;
  background-image: url("../../../../../assets/images/icons/blue/info-circle.svg");
  background-size: 32px;
  background-repeat: no-repeat;
  background-position: 26px center;
  padding: 1em 4em 1em 6em;
}
.EmbedBlock .cw-embedblock-about p {
  padding: 20px;
  line-height: 1.3em;
}
.EmbedBlock .cw-embedblock-time {
  display: none;
}
.EmbedBlock .cw-embedblock-time .cw-embedblock-time-start-min,
.EmbedBlock .cw-embedblock-time .cw-embedblock-time-end-min {
  margin-right: 14px;
}
section#courseware .block-content:not([data-view="student"]) .cw-embedblock-time input[type="number"] {
  width: 4em;
}
section#courseware .block-content:not([data-view="student"]) input.cw-embedblock-time-warning,
section#courseware .block-content:not([data-view="student"]) input.cw-embedblock-wrong-plattform {
  border: solid thick #d60000;
}
section#courseware .EmbedBlock button.cw-exclaim {
  display: none;
}
section#courseware.view-author .EmbedBlock .cw-embedblock-iframe {
  width: 860px !important;
}
section#courseware.view-author .EmbedBlock .cw-embedblock-image.full-width {
  width: 860px;
  height: auto;
}



