.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
section#courseware .active-section section.block.block-is-assorted,
section#courseware .aside-section section.block.block-is-assorted {
  display: none;
}
section#courseware.view-author .active-section section.block.block-is-assorted,
section#courseware.view-author .aside-section section.block.block-is-assorted {
  display: block;
  border: dashed thin #ccc;
}
section#courseware.view-author .active-section section.block.block-is-assorted:hover,
section#courseware.view-author .aside-section section.block.block-is-assorted:hover {
  border: dashed thin #000;
}
/* author mode */
.AssortBlock .cw-assort-description {
  background: #eee;
  margin: 1em;
  background-image: url("../../../../../assets/images/icons/blue/info-circle.svg");
  background-size: 32px;
  background-repeat: no-repeat;
  background-position: 26px center;
  padding-left: 64px;
}
.AssortBlock .cw-assort-description p {
  padding: 20px;
  line-height: 1.3em;
}
.AssortBlock .assorttypeselectors {
  margin-left: 5px;
  margin-bottom: 16px;
}
.AssortBlock .assorttypeselectors input[type="radio"] {
  margin-right: 16px;
  margin-top: -1px;
  margin-left: -1px;
  vertical-align: middle;
}
.AssortBlock .assortblock-list {
  padding-left: 0;
}
.AssortBlock .assortblock-list li {
  list-style: none;
  margin-bottom: 5px;
}
.AssortBlock .assortblock-list li label {
  min-width: 16em !important;
}
.AssortBlock .assortblock-list li input[type="text"] {
  margin-left: 16px;
}
/* END author mode*/
/* all tabs */
.AssortBlock .ui-tabs {
  border: thin solid #eaeaea;
}
.AssortBlock .ui-tabs .ui-state-active,
.AssortBlock .ui-tabs .ui-state-default {
  background-image: none;
  cursor: pointer !important;
  border: none;
}
.AssortBlock .ui-tabs .ui-state-active a,
.AssortBlock .ui-tabs .ui-state-default a {
  color: #fff;
  cursor: pointer !important;
}
.AssortBlock .ui-tabs .ui-state-default {
  background-color: #e9e9e9;
}
.AssortBlock .ui-tabs .ui-state-active {
  background-color: #fff;
}
.AssortBlock .ui-tabs .ui-widget-header {
  border: none;
  background-color: #e9e9e9;
}
/* END all tabs*/
/* vertical tabs */
.AssortBlock .ui-tabs-vertical {
  padding: 0;
}
.AssortBlock .ui-tabs-vertical .ui-tabs-nav {
  padding: 0.2em 0.1em 0.2em 0.2em;
  float: left;
}
.AssortBlock .ui-tabs-vertical .ui-tabs-nav li {
  clear: left;
  width: 100%;
  border-bottom-width: 1px !important;
  border-right-width: 0 !important;
  margin: 0 -1px 0.2em 0;
}
.AssortBlock .ui-tabs-vertical .ui-tabs-nav li a {
  display: block;
  width: 12em;
  color: #28497c;
}
.AssortBlock .ui-tabs-vertical .ui-tabs-nav .ui-tabs-active {
  padding-bottom: 0;
  border-right-width: 1px;
}
.AssortBlock .ui-tabs-vertical .ui-tabs-panel {
  padding: 1em;
  margin-bottom: -300px;
}
/* END vertical tabs*/
/* normal tabs */
.AssortBlock .ui-tabs {
  padding: 0;
}
.AssortBlock .ui-tabs .ui-tabs-nav {
  padding: 0.5em 0.5em 0 0.5em;
}
.AssortBlock .ui-tabs .ui-tabs-nav li a {
  color: #28497c;
}
/* END normal tabs*/
/* accordion */
.AssortBlock .assortblock-content.ui-accordion {
  border: solid thin #ccc;
}
.AssortBlock .ui-accordion-header {
  background-color: #eee;
  border: none !important;
  border-bottom: solid thin #ccc !important;
  padding: 1em 1em 1em 6px !important;
  color: #28497c;
}
.AssortBlock .ui-accordion-header span.ui-icon {
  margin-right: 0.5em;
}
.AssortBlock .ui-accordion-header.ui-accordion-header-collapsed:last-of-type {
  border-bottom: none !important;
}
.AssortBlock .ui-accordion-header.ui-accordion-header-active {
  font-weight: 600;
}
.AssortBlock .ui-accordion-content {
  padding: 1em !important;
  border: none !important;
  border-bottom: solid thin #ccc !important;
}
.AssortBlock .ui-accordion-content:last-of-type {
  border-bottom: none !important;
}
/* END accordion */
/* sidebar special */
.cw-sidebar .AssortBlock .ui-accordion-header {
  padding: 6px 6px 6px 3px !important;
}
.cw-sidebar .AssortBlock .cw-html-wrapper {
  border: none;
}
.cw-sidebar .AssortBlock .cw-html-wrapper .cw-html-header {
  display: none;
}



