.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.cw-folder-title {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
  border-bottom: none;
  margin-bottom: 0;
}
.cw-folder {
  border: solid thin #ccc;
  padding: 1em;
}
.cw-folder ul {
  padding: 0;
}
.cw-folder-file-item {
  list-style: none;
}
.cw-folder-file-item:not(:last-child) {
  border-bottom: solid thin #ccc;
}
.cw-folder-file-item a {
  display: block;
}
.cw-folder-file-item:hover {
  background-color: hsla(217, 6%, 45%, 0.2);
}
.cw-folder-file-info {
  background-image: url("../../../../../assets/images/icons/blue/file.svg");
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 24px;
  padding-left: 26px;
  margin: 1em;
  line-height: 24px;
  color: #28497c;
}
.cw-folder-file-info.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file.svg");
}
.cw-folder-file-info.cw-folder-fileicon-audio {
  background-image: url("../../../../../assets/images/icons/blue/file-audio.svg");
}
.cw-folder-file-info.cw-folder-fileicon-audio.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file-audio.svg");
}
.cw-folder-file-info.cw-folder-fileicon-pic {
  background-image: url("../../../../../assets/images/icons/blue/file-pic.svg");
}
.cw-folder-file-info.cw-folder-fileicon-pic.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file-pic.svg");
}
.cw-folder-file-info.cw-folder-fileicon-video {
  background-image: url("../../../../../assets/images/icons/blue/file-video.svg");
}
.cw-folder-file-info.cw-folder-fileicon-video.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file-video.svg");
}
.cw-folder-file-info.cw-folder-fileicon-pdf {
  background-image: url("../../../../../assets/images/icons/blue/file-pdf.svg");
}
.cw-folder-file-info.cw-folder-fileicon-pdf.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file-pdf.svg");
}
.cw-folder-file-info.cw-folder-fileicon-text {
  background-image: url("../../../../../assets/images/icons/blue/file-text.svg");
}
.cw-folder-file-info.cw-folder-fileicon-text.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file-text.svg");
}
.cw-folder-file-info.cw-folder-fileicon-ppt {
  background-image: url("../../../../../assets/images/icons/blue/file-ppt.svg");
}
.cw-folder-file-info.cw-folder-fileicon-ppt.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file-ppt.svg");
}
.cw-folder-file-info.cw-folder-fileicon-archive {
  background-image: url("../../../../../assets/images/icons/blue/file-archive.svg");
}
.cw-folder-file-info.cw-folder-fileicon-archive.cw-folder-file-disabled {
  background-image: url("../../../../../assets/images/icons/grey/file-archive.svg");
}
.cw-folder-file-info.cw-folder-file-disabled {
  color: #888;
}
.cw-folder-file-info span {
  color: lightgrey;
}
.cw-folder-file-details {
  color: #888;
}
.cw-folder-download-icon {
  background-image: url("../../../../../assets/images/icons/blue/download.svg");
  height: 24px;
  width: 24px;
  background-size: 24px;
  background-repeat: no-repeat;
  position: absolute;
  right: 2em;
  margin: 1em;
}
.cw-folder-download-icon.cw-folder-download-icon-disabled {
  background-image: url("../../../../../assets/images/icons/grey/download.svg");
}
.arrow.icon-role-clickable.icon-shape-arr_1down {
  display: none;
}
.cw-folder-allow-download-info {
  margin-top: -10px;
}
section#courseware .block-content:not([data-view="student"]) label {
  width: auto;
}
section#courseware .block-content:not([data-view="student"]) .select2-container {
  margin-left: 1em;
}
section#courseware .block-content:not([data-view="student"]) .select2-container .select2-dropdown {
  margin-left: -1em;
}
.cw-folder-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  background-repeat: no-repeat;
}
.cw-folder-icon.StandardFolder,
.cw-folder-icon.CoursePublicFolder,
.cw-folder-icon.PublicFolder {
  background-image: url("../../../../../assets/images/icons/blue/folder-empty.svg");
}
.cw-folder-icon.StandardFolder.full,
.cw-folder-icon.CoursePublicFolder.full,
.cw-folder-icon.PublicFolder.full {
  background-image: url("../../../../../assets/images/icons/blue/folder-full2.svg");
}
.cw-folder-icon.CourseGroupFolder {
  background-image: url("../../../../../assets/images/icons/blue/folder-group-empty.svg");
}
.cw-folder-icon.CourseGroupFolder.full {
  background-image: url("../../../../../assets/images/icons/blue/folder-group-full2.svg");
}
.cw-folder-icon.HomeworkFolder {
  background-image: url("../../../../../assets/images/icons/blue/folder-lock-empty.svg");
}
.cw-folder-icon.HomeworkFolder.full {
  background-image: url("../../../../../assets/images/icons/blue/folder-lock-full2.svg");
}
.cw-folder-icon.MaterialFolder {
  background-image: url("../../../../../assets/images/icons/blue/download.svg");
}
.cw-folder-icon.TimedFolder {
  background-image: url("../../../../../assets/images/icons/blue/folder-date-empty.svg");
}
.cw-folder-icon.TimedFolder.full {
  background-image: url("../../../../../assets/images/icons/blue/folder-date-full2.svg");
}
.cw-folder-icon.HiddenFolder {
  background-image: url("../../../../../assets/images/icons/grey/visibility-invisible/folder-lock-empty.svg");
}
.cw-folder-icon.HiddenFolder.full {
  background-image: url("../../../../../assets/images/icons/grey/visibility-invisible/folder-lock-full.svg");
}
.cw-folder-upload {
  margin-top: 1em;
  padding: 0.5em;
}
.cw-folder-upload .cw-folder-upload-filename {
  color: #28497c;
  padding: 1em;
  line-height: 36px;
  vertical-align: text-bottom;
  background-image: url("../../../../../assets/images/icons/blue/file.svg");
  background-position: 0.5em center;
  background-repeat: no-repeat;
  background-size: 24px;
  padding-left: 2.5em;
  padding-right: 1em;
}
.cw-folder-upload .cw-folder-upload-filename,
.cw-folder-upload .cw-folder-upload-button,
.cw-folder-upload .cw-folder-reselect-button,
.cw-folder-upload .cw-folder-file-upload {
  display: none;
}
.cw-folder-upload .cw-folder-select-button,
.cw-folder-upload .cw-folder-upload-button {
  background-image: url("../../../../../assets/images/icons/blue/upload.svg");
}
.cw-folder-upload .cw-folder-select-button:hover,
.cw-folder-upload .cw-folder-upload-button:hover {
  background-image: url("../../../../../assets/images/icons/white/upload.svg");
}
.cw-folder-upload .cw-folder-reselect-button {
  background-image: url("../../../../../assets/images/icons/blue/refresh.svg");
}
.cw-folder-upload .cw-folder-reselect-button:hover {
  background-image: url("../../../../../assets/images/icons/white/refresh.svg");
}
.cw-folder-upload .cw-folder-select-button,
.cw-folder-upload .cw-folder-reselect-button,
.cw-folder-upload .cw-folder-upload-button {
  color: #28497c;
  border: solid thin #28497c;
  background-color: #fff;
  min-height: 27px;
  line-height: 130%;
  min-width: 100px;
  padding: 5px 15px 5px 30px;
  margin: 0 0 0.25em 0;
  cursor: pointer;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  white-space: nowrap;
  width: auto;
  height: 35px;
  background-position: 16px center;
  font-size: 14px;
  padding-left: 48px;
  padding-right: 20px;
  background-repeat: no-repeat;
}
.cw-folder-upload .cw-folder-select-button:hover,
.cw-folder-upload .cw-folder-reselect-button:hover,
.cw-folder-upload .cw-folder-upload-button:hover {
  background-color: #28497c;
  color: #fff;
  outline: 0;
}
/* sidebar special */
.cw-sidebar .FolderBlock {
  padding: 0;
}
.cw-sidebar .FolderBlock .cw-folder-title {
  background: #e7ebf1;
  color: #28497c;
  font-weight: bold;
  font-size: unset;
  line-height: unset;
  text-align: left;
  padding: 4px;
  margin: 0 0 10px 0;
  border: none;
  position: unset;
}
.cw-sidebar .FolderBlock .cw-file-info {
  overflow: hidden;
}
.cw-sidebar .FolderBlock .cw-upload-label {
  vertical-align: bottom;
}
.cw-sidebar .FolderBlock .cw-folder-button-wrapper .cw-folder-download-button {
  min-width: 230px;
  max-width: 230px;
  background: transparent;
  opacity: 0;
}
.cw-sidebar .FolderBlock .cw-upload-checkbox {
  display: flex;
}
.cw-sidebar .FolderBlock .cw-upload-checkbox .cw-upload-label {
  margin-left: auto;
}



