.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.cw-audio-description {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
  border-bottom: none;
}
.cw-audio-container {
  border: solid thin #ccc;
  padding-top: 2em;
}
.cw-audio-controls {
  text-align: right;
  padding: 0 0.5em;
}
.cw-audio-range {
  margin: 0 5px 10px 0;
}
.cw-audio-range.ui-widget-content {
  background-color: #24437C;
}
.cw-audio-range .ui-widget-header {
  background-color: #fdfdfd;
}
.cw-audio-range .ui-slider-handle {
  border-radius: 20px;
  width: 1em;
  height: 1.7em;
  top: -0.5em;
  background-color: #ddd;
  border-color: #ccc;
  cursor: pointer;
  margin-left: -2px;
}
.cw-audio-playbutton,
.cw-audio-stopbutton {
  color: #28497c;
  border: solid thin #28497c;
  background-color: #fff;
  background-repeat: no-repeat;
  min-height: 27px;
  line-height: 130%;
  min-width: 100px;
  padding: 5px 15px 5px 30px;
  margin: 0 0 0.25em 0;
  cursor: pointer;
  font-size: 14px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  white-space: nowrap;
  width: auto;
  min-width: unset;
  margin: 5px;
  height: 46px;
  width: 46px;
  background-position: 6px center;
  display: inline-block;
}
.cw-audio-playbutton:hover,
.cw-audio-stopbutton:hover {
  background-color: #28497c;
  color: #fff;
  outline: 0;
}
.cw-audio-playbutton {
  background-image: url("../../../../../assets/images/icons/blue/play.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 28px;
}
.cw-audio-playbutton:hover {
  background-image: url("../../../../../assets/images/icons/white/play.svg");
}
.cw-audio-stopbutton {
  background-image: url("../../../../../assets/images/icons/blue/stop.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 24px;
}
.cw-audio-stopbutton:hover {
  background-image: url("../../../../../assets/images/icons/white/stop.svg");
}
.cw-audio-playbutton-playing {
  background-image: url("../../../../../assets/images/icons/blue/pause.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 24px;
}
.cw-audio-playbutton-playing:hover {
  background-image: url("../../../../../assets/images/icons/white/pause.svg");
}
.cw-audio-time {
  position: relative;
  top: -1em;
  color: #333;
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recorder-wrapper {
  display: inline-block;
  margin-top: 0.75em;
  vertical-align: bottom;
  margin-left: 1em;
}
section#courseware .block-content:not([data-view="student"]) select.cw-audioblock-source {
  width: 14em;
  margin-right: 1.75em;
}
section#courseware .block-content:not([data-view="student"]) label.cw-audioblock-description-label {
  padding-left: 1.75em;
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recorder-reset {
  background-image: url("../../../../../assets/images/icons/blue/trash.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 20px;
  background-color: #fff;
  height: 20px;
  width: 20px;
  border: none;
  vertical-align: top;
  cursor: pointer;
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recorder-start {
  background-image: url("../../../../../assets/images/icons/blue/span-full.svg");
  background-position: 6px center;
  padding-left: 2em;
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recorder-start:hover {
  background-image: url("../../../../../assets/images/icons/white/span-full.svg");
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recording-wrapper {
  background-image: url("../../../../../assets/images/icons/red/span-full.svg");
  color: #d60000;
  border-color: #d60000;
  background-position: 6px center;
  padding-left: 2em;
  cursor: progress;
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recording-wrapper:hover {
  background-color: #fff;
  color: #d60000;
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recorder-stop {
  background-image: url("../../../../../assets/images/icons/blue/stop.svg");
  background-position: 6px center;
  padding-left: 2em;
}
section#courseware .block-content:not([data-view="student"]) .cw-audioblock-recorder-stop:hover {
  background-image: url("../../../../../assets/images/icons/white/stop.svg");
}



