.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
section#courseware .block-content:not([data-view="student"]) fieldset.cw-chart-item-dataset {
  border: solid thin #ddd;
}
section#courseware .block-content:not([data-view="student"]) fieldset.cw-chart-item-dataset legend.cw-chart-item-title {
  color: #ccc;
}
section#courseware .block-content:not([data-view="student"]) fieldset.cw-chart-item-dataset label {
  width: unset;
  min-width: unset;
}
section#courseware .block-content:not([data-view="student"]) fieldset.cw-chart-item-dataset input.cw-chart-item-value,
section#courseware .block-content:not([data-view="student"]) fieldset.cw-chart-item-dataset select.cw-chart-item-color {
  width: 10em;
}
section#courseware .block-content:not([data-view="student"]) fieldset.cw-chart-item-dataset input.cw-chart-item-label {
  width: 14em;
}
section#courseware .block-content:not([data-view="student"]) button[name="additem"] {
  color: #28497c;
  border: solid thin #28497c;
  background-color: #fff;
  background-repeat: no-repeat;
  min-height: 27px;
  line-height: 130%;
  min-width: 100px;
  padding: 5px 15px 5px 30px;
  margin: 0 0 0.25em 0;
  cursor: pointer;
  font-size: 14px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  white-space: nowrap;
  width: auto;
  width: initial;
  text-align: left;
  background-image: url("../../../../../assets/images/icons/blue/add.svg");
  background-position: 6px center;
  padding-left: 28px;
}
section#courseware .block-content:not([data-view="student"]) button[name="additem"]:hover {
  background-color: #28497c;
  color: #fff;
  outline: 0;
}
section#courseware .block-content:not([data-view="student"]) button[name="additem"]:hover {
  background-image: url("../../../../../assets/images/icons/white/add.svg");
}
section#courseware .block-content:not([data-view="student"]) button[name="removeitem"] {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
  background: url("../../../../../assets/images/icons/blue/trash.svg") 50% no-repeat;
  margin-left: 1em;
  cursor: pointer;
}



