.cw-keypoint {
  background-repeat: no-repeat;
  background-size: 48px;
  background-position-x: 2em;
  background-position-y: center;
  min-height: 52px;
  padding: 1.5em 2.5em 1.5em 8em;
  border-style: solid;
  border-width: 2px;
}
.cw-keypoint-sentence {
  font-size: 1.25em;
  padding-top: 0.75em;
}
.cw-keypoint-select-icon option {
  padding-left: 2em;
  background-repeat: no-repeat;
}
.cw-keypoint-label-color {
  width: 32px !important;
  min-width: 32px !important;
  height: 32px;
  padding: 5px !important;
}
.cw-keypoint-label-color > .cw-keypoint-input-color {
  visibility: hidden;
  position: absolute;
}
.cw-keypoint-label-color > .cw-keypoint-input-color + div {
  cursor: pointer;
  border: 2px solid transparent;
  height: 32px;
  width: 32px;
}
.cw-keypoint-label-color > .cw-keypoint-input-color:checked + div {
  /* (RADIO CHECKED) IMAGE STYLES */
  background-image: url("../../../../../assets/images/icons/white/accept.svg");
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: center;
}
.cw-keypoint-label-wrapper-color,
.cw-keypoint-label-wrapper-icon {
  padding: 6px 0 6px 5px;
  width: 30em;
  display: inline-block;
}
.cw-keypoint-label-wrapper-color {
  padding-left: 0.7em;
}
.cw-keypoint-label-wrapper-icon {
  margin-left: -0.5em;
}
label[for="cw-keypoint-icon"],
label[for="cw-keypoint-color"] {
  vertical-align: top;
}
.cw-keypoint-label-icon {
  width: 4em !important;
  min-width: 4em !important;
  padding: 0.5em !important;
}
.cw-keypoint-label-icon > .cw-keypoint-input-icon {
  visibility: hidden;
  position: absolute;
}
.cw-keypoint-label-icon > .cw-keypoint-input-icon + div {
  cursor: pointer;
  border: 2px solid transparent;
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: top center;
  width: 5em;
  height: 3em;
  line-height: 5em;
  text-align: center;
  font-size: 0.9em;
  color: #abb7ce;
}
.cw-keypoint-label-icon > .cw-keypoint-input-icon:checked + div {
  /* (RADIO CHECKED) IMAGE STYLES */
  color: #24437c;
  font-weight: 600;
}
.cw-keypoint-label-icon > .cw-keypoint-input-icon:checked + .cw-keypoint-icon-accept {
  background-image: url("../../../../../assets/images/icons/blue/accept.svg");
}
.cw-keypoint-label-icon > .cw-keypoint-input-icon:checked + .cw-keypoint-icon-decline {
  background-image: url("../../../../../assets/images/icons/blue/decline.svg");
