.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.content iframe {
  border: solid thin #ccc;
}
.iframe-author-view,
.iframe-data {
  margin: 0 1em;
}
.iframe-author-view label,
.iframe-data label {
  width: 15em;
}
.iframe-author-view .submit-user-id-switch,
.iframe-data .submit-user-id-switch {
  width: 1em !important;
}
.iframe-description {
  background: #eee;
  margin: 1em 0;
  background-image: url("../../../../../assets/images/icons/blue/info-circle.svg");
  background-repeat: no-repeat;
  background-size: 32px;
  background-position: 26px center;
  padding-left: 64px;
}
.iframe-description p {
  padding: 20px;
  line-height: 1.3em;
}
.iframe-submit-user {
  display: none;
}
.iframe-cc-wrapper {
  padding: 0;
}
.iframe-cc-wrapper input[type="checkbox"] {
  margin-right: 2em !important;
  vertical-align: sub;
}
.iframe-cc-info input {
  width: 18em !important;
}
.iframe-cc-selector select {
  width: 26em !important;
}
.iframe-cc-icon {
  width: 120px;
  height: 50px;
  display: inline-block;
  background-repeat: no-repeat;
  margin-left: 2em;
}
.iframe-cc-icon.default {
  background-image: url("../images/icons/cc/cc.srr.primary.svg");
}
.iframe-cc-icon.by {
  background-image: url("../images/icons/cc/by.svg");
}
.iframe-cc-icon.by-nc {
  background-image: url("../images/icons/cc/by-nc.eu.svg");
}
.iframe-cc-icon.by-nc-nd {
  background-image: url("../images/icons/cc/by-nc-nd.eu.svg");
}
.iframe-cc-icon.by-nc-sa {
  background-image: url("../images/icons/cc/by-nc-sa.svg");
}
.iframe-cc-icon.by-nd {
  background-image: url("../images/icons/cc/by-nd.svg");
}
.iframe-cc-icon.by-sa {
  background-image: url("../images/icons/cc/by-sa.svg");
}
.iframe-cc-data {
  border: solid thin #ccc;
  margin-top: -5px;
  padding-top: 10px;
  height: 75px;
}
.iframe-cc-infos-student {
  display: inline-block;
  padding-left: 2em;
}



