.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
textarea.cw-typewriter-content {
  width: 96%;
  height: 12em;
  overflow-y: auto;
  overflow-x: hidden;
  resize: none;
  vertical-align: baseline;
  margin-left: 1em;
}
.cw-typewriter {
  white-space: pre-wrap;
}
.cw-typewriter.font-typewriter {
  font-family: "Lucida Sans Typewriter", "Lucida Console", Monaco, "Bitstream Vera Sans Mono", monospace;
}
.cw-typewriter.font-trebuchet {
  font-family: "Trebuchet MS", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", sans-serif;
}
.cw-typewriter.font-tahoma {
  font-family: Tahoma, Verdana, Segoe, sans-serif;
}
.cw-typewriter.font-georgia {
  font-family: Georgia, Times, "Times New Roman", serif;
}
.cw-typewriter.font-narrow {
  font-family: "Arial Narrow", Arial, "Helvetica Condensed", Helvetica, sans-serif;
}
.cw-typewriter.size-tall {
  font-size: 1.5em;
  line-height: 1.25em;
}
.cw-typewriter.size-grande {
  font-size: 2em;
  line-height: 1.25em;
}
.cw-typewriter.size-huge {
  font-size: 4em;
  line-height: 1.25em;
}
.cw-sidebar textarea.cw-typewriter-content {
  width: 98%;
}



