.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.cw-search-author-view {
  margin: 0 1em;
}
.cw-search-description {
  background: #eee;
  margin: 1em 0;
  background-image: url("../../../../../assets/images/icons/blue/info-circle.svg");
  background-size: 32px;
  background-repeat: no-repeat;
  background-position: 26px center;
  padding-left: 64px;
}
.cw-search-description p {
  padding: 20px;
  line-height: 1.3em;
}
.cw-search-data li {
  list-style: none;
  margin-left: -2.5em;
  padding-bottom: 0.8em;
}
.cw-search-data label {
  display: inline-block;
  width: 5em;
  text-align: left;
}
.cw-search-data input[type="checkbox"] {
  vertical-align: middle;
}
.cw-search-title {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
}
.cw-search-input-search {
  width: 100%;
  max-width: 745px;
  margin-right: 25px;
  padding: 0.5em;
  vertical-align: middle;
  border: solid thin #ccc;
}
.cw-search-input-search:focus {
  border-color: #009eff;
}
.cw-search-result .cw-search-result-empty {
  display: none;
}
.cw-search-result ul {
  margin-top: 0.5em;
}
.cw-search-result li {
  list-style: none;
  margin-left: -3em;
  padding: 0.5em;
  padding-left: 1.5em;
  background-image: url("../../../../../assets/images/icons/blue/arr_1right.svg");
  background-position: 3px center;
  background-repeat: no-repeat;
  background-color: #eee;
  background-size: 16px;
}
.cw-search-result li:nth-child(2n+1) {
  background-image: url("../../../../../assets/images/icons/blue/arr_1right.svg");
  background-position: 3px center;
  background-repeat: no-repeat;
  background-color: #f8f8f8;
  background-size: 16px;
}
/* mobile */
@media screen and (max-width: 800px) {
  .cw-search-input-search {
    width: 20em;
    margin-right: 1em;
  }
}
/* sidebar special */
.cw-sidebar .cw-search {
  border: 1px solid #d0d7e3;
}
.cw-sidebar .cw-search .cw-search-title {
  background: #e7ebf1;
  color: #28497c;
  font-weight: bold;
  font-size: unset;
  line-height: unset;
  text-align: left;
  padding: 4px;
  margin: 0 0 10px 0;
  border: none;
  position: unset;
}
.cw-sidebar .cw-search .cw-search-input-search {
  width: 12em;
  margin: 0 0.5em;
  font-size: 0.8em;
}
.cw-sidebar .cw-search-author-view .cw-search-description {
  display: none;
}
.cw-sidebar .cw-search-author-view .cw-search-input-title {
  max-width: 185px;
}



