.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
section.BlubberBlock div.blubber-stream.loading {
  background-image: url("../../../../../assets/images/ajax_indicator_small.gif");
  background-position: left;
  background-repeat: no-repeat;
  padding-left: 20px;
  opacity: 0.5;
}
section.BlubberBlock input[type=submit] {
  float: right;
}



