.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
.CanvasBlock .cw-canvasblock-canvas {
  max-width: 100%;
  border: solid thin #ccc;
}
.CanvasBlock .cw-canvasblock-upload-message {
  display: none;
}
.CanvasBlock .cw-canvasblock-original-img {
  display: none;
}
.CanvasBlock .cw-canvasblock-tool-selected-pen {
  cursor: url("../../assets/images/cursor/pencil.png") 0 32, auto;
}
.CanvasBlock .cw-canvasblock-tool-selected-text {
  cursor: text;
}
.CanvasBlock h1.cw-canvasblock-description {
  min-width: 10%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-style: normal;
  font-weight: 600;
  color: #28497c;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 20px;
  position: relative;
  margin: 0;
  text-align: center;
  border: solid thin #ccc;
  background-color: #fcfcfc;
  border-bottom: none;
}
.CanvasBlock .cw-canvasblock-toolbar {
  border: solid thin #ccc;
  border-bottom: none;
}
.CanvasBlock .cw-canvasblock-buttonset {
  display: inline-block;
  padding: 5px;
  margin-right: 0.5em;
}
.CanvasBlock button {
  cursor: pointer;
  border: solid thin #ccc;
  height: 32px;
  width: 32px;
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px 24px;
}
.CanvasBlock button.cw-canvasblock-color.selected-color {
  border: solid 2px #000;
}
.CanvasBlock button.cw-canvasblock-reset {
  background-image: url("../../../../../assets/images/icons/blue/refresh.svg");
}
.CanvasBlock button.cw-canvasblock-size {
  background-image: url("../../../../../assets/images/icons/blue/stop.svg");
}
.CanvasBlock button.cw-canvasblock-size.cw-canvasblock-size-small {
  background-size: 8px 7px;
}
.CanvasBlock button.cw-canvasblock-size.cw-canvasblock-size-normal {
  background-size: 16px 14px;
}
.CanvasBlock button.cw-canvasblock-size.cw-canvasblock-size-large {
  background-size: 22px 20px;
}
.CanvasBlock button.cw-canvasblock-size.cw-canvasblock-size-huge {
  background-size: 26px 24px;
}
.CanvasBlock button.cw-canvasblock-size.selected-size {
  border: solid 2px #000;
}
.CanvasBlock button.cw-canvasblock-tool.cw-canvasblock-tool-pen {
  background-image: url("../../../../../assets/images/icons/blue/comment.svg");
}
.CanvasBlock button.cw-canvasblock-tool.cw-canvasblock-tool-text {
  vertical-align: top;
  font-size: 22px;
  color: #28497c;
  font-weight: 600;
}
.CanvasBlock button.cw-canvasblock-tool.selected-tool {
  border: solid 2px #000;
}
.CanvasBlock button.cw-canvasblock-undo {
  background-image: url("../../../../../assets/images/icons/blue/arr_2left.svg");
}
.CanvasBlock button.cw-canvasblock-download {
  background-image: url("../../../../../assets/images/icons/blue/download.svg");
}
.CanvasBlock button.cw-canvasblock-store {
  background-image: url("../../../../../assets/images/icons/blue/upload.svg");
}
.CanvasBlock button.cw-canvasblock-show-all {
  background-image: url("../../../../../assets/images/icons/blue/group2.svg");
}
.CanvasBlock button.cw-canvasblock-show-all.selected-view {
  border: solid 2px #000;
}
.CanvasBlock button.cw-canvasblock-show-own {
  background-image: url("../../../../../assets/images/icons/blue/headache.svg");
}
.CanvasBlock button.cw-canvasblock-show-own.selected-view {
  border: solid 2px #000;
}
section#courseware .block-content:not([data-view="student"]) select.cw-canvasblock-source {
  width: 14em;
  margin-right: 1em;
}



