.btn-base {
  width: 20px;
  height: 20px;
  color: transparent;
  border: none;
}
.btn-base:hover {
  cursor: pointer;
}
section.HtmlBlock .controls button.no-sorting {
  visibility: hidden;
}
section.HtmlBlock textarea {
  width: 100%;
}
section.HtmlBlock .cke_editable {
  min-height: 12em;
}
section.HtmlBlock div.content img {
  max-width: 100%;
}
section.HtmlBlock div.content:after {
  content: ".";
  height: 0;
  display: block;
  visibility: hidden;
  clear: both;
}
/* sidebar special */
.cw-sidebar .cw-html-wrapper {
  border: 1px solid #d0d7e3;
}
.cw-sidebar .cw-html-wrapper .cw-html-header {
  background: #e7ebf1;
  color: #28497c;
  font-weight: bold;
  font-size: unset;
  line-height: unset;
  text-align: left;
  padding: 4px;
  margin: 0 0 10px 0;
  border: none;
  position: unset;
  height: 20px;
}
.cw-sidebar .content {
  padding: 0 5px;
}



